import React, { useEffect } from 'react';
import tw, { styled } from 'twin.macro';
import { graphql, useStaticQuery } from 'gatsby';
import Layout from '../components/Layout';
import Header from '../components/Header';
import BREAKPOINTS from '../styles/breakpoints';

const ContactContainer = styled.div`
  ${tw`
    text-center
  `}
  padding: 4rem 1.5rem 2rem;
  max-width: 1088px;
  margin: 0 auto;
  @media (min-width: ${BREAKPOINTS.md}px) {
    padding: 5rem 0 2rem;
  }
`;
const FormWrapper = styled.div`
  max-width: 584px;
  margin: 0 auto;

  div {
    max-width: none;
  }

  input {
    border-radius: 8px;
  }
`;
const Title = styled.h1`
  ${tw`
    text-white
  `}
  font-size: 3rem;
  font-weight: 700;
  line-height: 4rem;
  margin-bottom: 2rem;
`;
const SubTitle = styled.h2`
  ${tw`
  text-gray40
  `}
  font-size: 1.75rem;
  font-weight: 500;
  line-height: 3rem;
  margin-bottom: 3rem;
`;

export default function Contact(): React.ReactElement {
  const { content } = useStaticQuery(
    graphql`
      query {
        content: markdownRemark(frontmatter: { slug: { eq: "contact" } }) {
          frontmatter {
            title
          }
          rawMarkdownBody
        }
      }
    `,
  );

  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://webforms.pipedrive.com/f/loader';
    script.defer = true;
    document.body.appendChild(script);
  }, []);

  return (
    <Layout>
      <Header />
      <ContactContainer>
        <Title>{content.frontmatter.title}</Title>
        <SubTitle>{content.rawMarkdownBody}</SubTitle>
        <FormWrapper>
          <div
            className="pipedriveWebForms"
            data-pd-webforms="https://webforms.pipedrive.com/f/1r5Zbv3nhXgwNpyOQrePG6xH0guwfdLM9OaAeNPzk856f8tT2IROcy97zLmu4IkRJ"
          />
        </FormWrapper>
      </ContactContainer>
    </Layout>
  );
}
